import React from 'react'
import Modal from 'components/Modal'
import Button from 'components/Button'
import FontIcon from 'components/FontIcon'
import { PropTypes } from 'prop-types'
import styles from './confirmEmail.module.scss'

const ConfirmEmail = ({ email, isOpen, toggle, onSubmit }) => (
  <Modal isOpen={isOpen} toggle={toggle} size="md" centered>
    <div className={styles.root}>
      <div className={styles.container}>
        <FontIcon name="envelope-open-text" className={styles.icon} />
        <h2 className={styles.title}>–ü–æ—Ç–≤—Ä–¥–∏—Ç–µ –≤–∞—à—É –µ-–∞–¥—Ä–µ—Å—É</h2>
        <p className={styles.desc}>
          –ü–æ—Å–ª–∞–ª–∏ —Å–º–æ –µ–ª–µ–∫—Ç—Ä–æ–Ω—Å–∫—É –ø–æ—à—Ç—É –Ω–∞ {'\t'}
          <a href={`mailto:${email}`}>{email}</a>
          {'\t'}
          –∫–∞–∫–æ –±–∏ –ø–æ—Ç–≤—Ä–¥–∏–ª–∏ –≤–∞—à—É –µ-–∞–¥—Ä–µ—Å—É. –ù–∞–∫–æ–Ω —à—Ç–æ –ø—Ä–∏–º–∏—Ç–µ –µ-–ø–æ—à—Ç—É, –∫–ª–∏–∫–Ω–∏—Ç–µ
          –Ω–∞ –ª–∏–Ω–∫ –∫–∞–∫–æ –±–∏—Å—Ç–µ –¥–æ–≤—Ä—à–∏–ª–∏ —Ä–µ–≥–∏—Å—Ç—Ä–∞—Ü–∏—ò—É.
        </p>
        <Button
          label="–ù–∞–∑–∞–¥ –Ω–∞ –ø—Ä–∏—ò–∞–≤—É"
          className={`btn-pink-fill ${styles.backButton}`}
          onClick={onSubmit}
        />
      </div>
    </div>
  </Modal>
)

ConfirmEmail.propTypes = {
  email: PropTypes.string,
  isOpen: PropTypes.bool,
  toggle: PropTypes.func,
  onSubmit: PropTypes.func,
}

ConfirmEmail.defaultProps = {
  email: '',
  isOpen: false,
  toggle: () => null,
  onSubmit: () => null,
}

export default ConfirmEmail
